/* Basic Theme */
// colors
$background: #000;
$color: #fff;

// fonts
$font-family: "Helvetica Neue", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", sans-serif;
$font-weight: 300;
$phone-text-size: 12pt;
$text-size: 16pt;
$line-height: 1.5;

/* Responsive Sizing */
//max
$max-phone: 419px;
$max-tablet: 767px;
$max-laptop: 1023px;
$max-desktop: 1199px;
//min
$min-phone: 420px;
$min-tablet: 768px;
$min-laptop: 1024px;
$min-desktop: 1200px;
